        ></path>
      </svg>
    </div>

    <!-- 侧边导航 -->
    <div :class="{ hidden: !isVisible }" class="side-menu-div">
      <SideMenu></SideMenu>
    </div>

    <!-- 主体内容 -->
    <div class="footprint-div">
      <div class="title" id="footprint">
        <label class="linear-gradient-text">👣 足 迹 </label><br /><br />
