      </div>
      <el-row>
        <el-col
          v-for="(item, index) in blogContent"
          :key="item"
          :span="writingSpan"
          :offset="writingSpan === 6 ? (index % 3 !== 0 ? 1 : 2) : 2"
        >
          <a
            :href="item.turnRoute"
            target="_blank"
            style="text-decoration: none"
          >
            <el-card
              shadow="hover"
              :body-style="{ padding: '5px' }"
              style="margin-bottom: 5vh"
            >
              <img :src="item.picPath" class="image" :alt="item.text" />
              <div style="padding-bottom: 14px; text-align: center">
                <h4>{{ item.text }}</h4>
              </div>
            </el-card>
          </a>
