<script setup lang="ts">
import SideMenu from "@/components/SideMenu.vue";
import TimeLine from "@/components/TimeLine.vue";
import ShowCase from "@/components/ShowCase.vue";
import { onMounted, onUnmounted, ref } from "vue";
import TypeIt from "typeit";

const text = ref("");
const title1 = ref("");
const title2 = ref("");
const title3 = ref("");
const isVisible = ref(true);
let timeoutId: number | null | undefined = null;

const hideMenu = () => {
  isVisible.value = false;
};

const showMenu = () => {
  isVisible.value = true;
  if (timeoutId) {
    clearTimeout(timeoutId);
  }
  timeoutId = setTimeout(hideMenu, 5000);
};

const handleScroll = () => {
  showMenu();
};
const blogContent = [
  {
    picPath: require("../assets/suzime.jpeg"),
    text: "荧幕后的万千世界",
    turnRoute: "https://v6bn1welpm.feishu.cn/docx/Dh2cdd1iOoLvzhxnJJBcq4XqnDI",
  },
  {
    picPath: require("../assets/atk.jpg"),
    text: "人类从历史中学到的",
    turnRoute: "https://www.bilibili.com/video/BV1494y1e7bV",
  },
  {
    picPath: require("../assets/cover.png"),
    text: "Research Page",
    turnRoute: "#/research",
  },
];
onMounted(() => {
  new TypeIt(text.value, {
    speed: 150,
    deleteSpeed: 50,
    cursorChar: "<span class='cursorChar'>|<span>", //用于光标的字符。HTML也可以
    lifeLike: true, // 使打字速度不规则
    cursor: false, //在字符串末尾显示闪烁的光标
    breakLines: false, // 控制是将多个字符串打印在彼此之上，还是删除这些字符串并相互替换
    loop: true, //是否循环
  })
    .type("欢迎来到邵国诚的个人网站！", { delay: 2000 })
    .delete("", { delay: 1000 })
    .type("点击任意位置以启用背景音乐~", { delay: 2000 })
    .delete("", { delay: 12000 })
    .go();

  new TypeIt(title1.value, {
    speed: 150,
    deleteSpeed: 50,
    lifeLike: true,
    loop: true,
  })
    .type("旅程总有一天会迎来终点，所以，不必匆忙", { delay: 5000 })
    .delete("", { delay: 2000 })
    .type("风带来故事的种子，时间使之发芽", { delay: 5000 })
    .delete("", { delay: 2000 })
    .go();

  new TypeIt(title2.value, {
    speed: 150,
    deleteSpeed: 50,
    lifeLike: true,
    loop: true,
  })
    .type("旅人将遍历的一万个故事，都将成为旅人灵魂的一部分", { delay: 5000 })
    .delete("", { delay: 2000 })
    .type("所谓的平凡生活，其实是日复一日的奇迹", { delay: 5000 })
    .delete("", { delay: 2000 })
    .type(
      "就算命运是注定的，人在路途中能做的事仍然有很多，而故事的结局也会呈现截然不同的意义",
      { delay: 5000 }
    )
    .delete("", { delay: 2000 })
    .type("岁月流转，唯有思念与归风不变", { delay: 5000 })
    .delete("", { delay: 2000 })
    .go();

  new TypeIt(title3.value, {
    speed: 150,
    deleteSpeed: 50,
    lifeLike: true,
    loop: true,
  })
    .type("艺术，让人成为人", { delay: 5000 })
    .delete("", { delay: 2000 })
    .type("信息熵即为灵魂", { delay: 5000 })
    .delete("", { delay: 2000 })
    .go();
});

const bgaudio = ref();
const playAudio = () => {
  if (bgaudio.value && bgaudio.value.paused) {
    bgaudio.value.play();
  }
};

const writingSpan = ref(6);

const updateWritingSpan = () => {
  if (window.innerWidth <= 768) {
    writingSpan.value = 20;
  } else {
    writingSpan.value = 6;
  }
};
onMounted(() => {
  // 目前许多浏览器不允许自动播放音频，只有用户点击屏幕后才可以播放
  document.addEventListener("click", playAudio);
  window.addEventListener("scroll", handleScroll);
  updateWritingSpan();
  window.addEventListener("resize", updateWritingSpan);
});

onUnmounted(() => {
  document.removeEventListener("click", playAudio);
  window.removeEventListener("scroll", handleScroll);
  window.removeEventListener("resize", updateWritingSpan);
  if (timeoutId) {
    clearTimeout(timeoutId);
  }
});
</script>

